*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

ul, ol {
  padding-left: 0;
}

html, body {
  overflow-x: hidden;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  line-height: 1;
}

ul, ol {
  list-style: none;
}

a {
  color: currentColor;
}

a[class] {
  text-decoration: none;
}

a:not([class]) {
  -webkit-text-decoration-skip-ink: auto;
  text-decoration-skip-ink: auto;
  color: currentColor;
}

img, picture {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

article > * + * {
  margin-top: 1em;
}

input, button, textarea, select {
  font: inherit;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  padding: 0;
}

address {
  font-style: normal;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.container {
  width: 100%;
  max-width: 375px;
  margin-left: auto;
  margin-right: auto;
  padding: 40px 20px 0;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.activ-page {
  color: #111;
  background: #eac645;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  display: flex;
}

.header-container {
  height: 72px;
  max-width: 375px;
  z-index: 2;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  padding: 18px 20px;
  display: flex;
  position: relative;
}

.nav-container {
  align-items: center;
  gap: 40px;
  display: flex;
}

.navigation {
  align-items: center;
  gap: 76px;
  display: flex;
}

.logo {
  width: 109px;
  height: 28px;
}

.logo-link {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo-text {
  fill: currentColor;
}

.menu {
  align-items: center;
  gap: 8px;
  font-weight: 500;
  display: flex;
}

.menu-link {
  text-transform: uppercase;
  letter-spacing: -.56px;
  border-radius: 36px;
  align-items: center;
  padding: 8px 16px;
  font-size: 14px;
  font-style: normal;
  line-height: 1.29;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.menu-link:hover, .menu-link:focus {
  color: #4f2ee8;
}

.shopping-list-icon {
  fill: currentColor;
}

.shopping-link {
  gap: 4px;
  display: flex;
}

.sign-up-container {
  gap: 24px;
  display: flex;
}

.user-settings {
  align-items: center;
  gap: 24px;
  display: flex;
}

.sign-up-block, .header-menu {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-container {
    min-width: 768px;
  }

  .sign-up-block, .header-menu {
    display: block;
  }

  .nav-container {
    gap: 40px;
  }

  .header-container {
    padding: 14px 32px;
  }
}

@media screen and (min-width: 1440px) {
  .header-container {
    min-width: 1440px;
    padding: 14px 24px;
  }

  .nav-container {
    gap: 76px;
  }
}

.dark .header-container {
  border: 2px solid #fff;
}

.dark .categories-list-name {
