.aside {
  width: 337px;
}

@media screen and (min-width: 768px) {
  .aside {
    width: 356px;
  }
}

.aside-list {
  height: 186px;
  flex-direction: column;
  gap: 24px;
  display: flex;
  overflow-y: scroll;
}

@media screen and (min-width: 768px) {
  .aside-list {
    height: 600px;
    gap: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .aside-list {
    height: 416px;
  }
}

.aside-link {
  color: rgba(17, 17, 17, .6);
  letter-spacing: -.32px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.aside-link:hover, .aside-link :focus {
