}

@media screen and (min-width: 768px) {
  .aside-link {
    letter-spacing: -.36px;
    font-size: 18px;
    line-height: 24px;
  }
}

.activ-name {
  color: #4f2ee8;
  text-transform: uppercase;
  font-weight: 700;
}

.aside-list {
