}

.aside-list::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 12px;
}

.books-section-title {
  text-align: left;
  color: #111;
  letter-spacing: -1.28px;
  margin-bottom: 40px;
  font-family: DM Sans;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
}

.card-list {
  flex-direction: column;
  display: flex;
}

@media screen and (min-width: 768px) {
  .card-list {
    flex-flow: wrap;
  }
}

.cards-item {
  margin-bottom: 40px;
}

.cards-item:nth-last-child(-n+1) {
  margin-bottom: 0;
}

@media screen and (min-width: 768px) {
  .cards-item {
    width: 218px;
    margin-right: 25px;
  }

  .cards-item:nth-child(3n) {
    margin-right: 0;
  }

  .cards-item:nth-last-child(-n+3) {
    margin-bottom: 0;
  }
}

@media screen and (min-width: 1440px) {
  .cards-item {
    width: 170px;
    margin-right: 24px;
  }

  .cards-item:nth-child(3n) {
    margin-right: 24px;
  }

  .cards-item:nth-child(5n) {
    margin-right: 0;
  }

  .cards-item:nth-last-child(-n+3) {
    margin-bottom: 40px;
  }

  .cards-item:nth-last-child(-n+5) {
    margin-bottom: 0;
  }
}

.cards-link {
  cursor: default;
}

.cards-header {
  border-radius: 8px;
  margin-bottom: 14px;
  overflow: hidden;
}

.cards-img {
  width: 100%;
  height: 485px;
  cursor: pointer;
}

@media screen and (min-width: 768px) {
  .cards-img {
    height: 316px;
  }
}

@media screen and (min-width: 1440px) {
  .cards-img {
    height: 256px;
  }
}

.cards-title {
  color: #111;
  letter-spacing: -.64px;
  text-transform: uppercase;
  cursor: pointer;
  margin-bottom: 4px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
}

.cards-author {
  color: #b4afaf;
  letter-spacing: -.48px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

.card-wrapper {
  width: 100%;
  border: none;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
}

.card-wrapper:hover {
  transform: scale(1.03);
}

.card-wrapper:hover .hover-text-card {
  visibility: visible;
}

.hover-text-card {
  width: 100%;
  color: #111;
  text-align: center;
  text-transform: uppercase;
  visibility: hidden;
  z-index: 9;
  pointer-events: none;
  background-color: #eac645;
  border-radius: 0 0 8px 8px;
  padding: 26px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
}

.section-support {
  padding-top: 40px;
}

.section-support > .container {
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  flex-shrink: 0;
  padding: 24px 40px 65px;
  position: relative;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}

.header-support {
  color: #fff;
  text-transform: capitalize;
  align-items: center;
  margin-bottom: 40px;
  display: flex;
}

.logo-index {
  color: #fff;
  letter-spacing: -.56px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.img-support {
  stroke: #fff;
  margin-left: 12px;
  display: block;
}

.charity-logos {
  width: 100%;
  height: calc(100% - 40px);
  overflow: hidden;
}

.support-link {
  align-items: center;
  display: flex;
}

.support-link-url {
  align-items: center;
  gap: 16px;
  display: flex;
}

.support-link-url .support-link-img {
  filter: brightness(0) invert();
  transition: transform .3s;
}

.support-link-url .support-link-img:hover {
  transform: scale(1.1);
}

.icon-button {
  fill: #4f2ee8;
}

.support-button {
  height: 38px;
  width: 38px;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.icon-button-angle {
  fill: #4f2ee8;
  transition: transform .3s;
}

@media screen and (max-width: 767px) {
  .section-support > .container {
    max-width: 335px;
    min-height: 352px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1439px) {
  .section-support > .container {
    min-height: 472px;
  }

  .swiper-backface-hidden {
