  }
}

@media screen and (min-width: 1440px) {
  .section-support {
    padding-top: 86px;
  }

  .section-support > .container {
    max-width: 356px;
    min-height: 490px;
  }

  .swiper-backface-hidden {
    height: 418px;
  }
}

.swiper {
  width: 100%;
  height: 100%;
  position: static;
}

.swiper-wrapper {
  flex-direction: column;
  position: static;
}

.swiper-backface-hidden {
  height: 280px;
  display: flex;
}

.swiper-vertical > .swiper-scrollbar {
  background: #eac645;
}

.swiper-wrapper > .swiper-slide {
  display: flex;
}

.swiper-scrollbar-drag {
  width: 10px;
}

.bestsellers-title {
  color: #111;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.19;
}

.bestsellers-title-accent {
  color: #4f2ee8;
}

.book-title {
  color: #b4afaf;
  text-transform: uppercase;
  margin-bottom: 18px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.book-image {
  width: 100%;
  height: 256px;
  background-position: center;
  background-size: cover;
  border-radius: 8px;
  margin-bottom: 14px;
  display: block;
  position: relative;
}

.book-subtitle {
  text-transform: uppercase;
  color: #111;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
  cursor: pointer;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.16;
  overflow: hidden;
}

.book-autor {
  text-align: left;
  color: #b4afaf;
  margin-bottom: 8px;
  font-size: 12px;
  font-style: italic;
  line-height: 1.17;
}

.more-btn {
  text-transform: uppercase;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  margin-bottom: 40px;
  margin-left: auto;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.more-btn:hover {
  color: #fff;
  background-color: #4f2ee8;
}

.card-wrapper-btn {
  width: 100%;
  border: none;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
}

.card-wrapper-btn:hover {
  transform: scale(1.03);
}

.card-wrapper-btn:hover .hover-text {
  visibility: visible;
}

.hover-text {
  width: 100%;
  color: #111;
  text-align: center;
  text-transform: uppercase;
  visibility: hidden;
  z-index: 9;
  pointer-events: none;
  background-color: #eac645;
  border-radius: 0 0 8px 8px;
  padding: 26px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -20%);
}

.top-btn {
  width: 54px;
  height: 54px;
  background-color: #4f2ee8;
  border-color: rgba(79, 46, 232, .3);
  border-radius: 50%;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
}

.book-info {
  gap: 25px;
  display: flex;
}

.loader {
  width: 50px;
  height: 50px;
  box-sizing: border-box;
  border: 8px solid rgba(79, 46, 232, .3);
  border-bottom-color: #4f2ee8;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

.loader-inner {
  min-height: 480px;
  justify-content: center;
  align-items: center;
  display: none;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .card-item {
    width: 100%;
  }

  .book-image {
    height: 485px;
  }
}

@media screen and (min-width: 768px) {
  .card-item {
    width: calc(33.3333% - 16.6667px);
  }

  .book-image {
    height: 316px;
  }

  .more-btn {
    margin-top: 20px;
  }

  .hover-text {
    bottom: 0;
  }

  .loader-inner {
    justify-content: end;
  }
}

@media screen and (min-width: 1440px) {
  .grid-wrapper {
    max-width: 996px;
  }

  .card-item {
    width: calc(20% - 19.2px);
  }

  .book-image {
    height: 256px;
  }

  .loader-inner {
    position: absolute;
    top: 50%;
    left: 65%;
    transform: translate(-65%, -50%);
  }
