.modal-open {
  height: 100vh;
  overflow-y: hidden;
}

.backdrop {
  width: 100%;
  height: 100vh;
  opacity: 1;
  z-index: 20;
  background: rgba(17, 17, 17, .4);
  justify-content: center;
  align-items: start;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.modal {
  min-width: 335px;
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 15px;
  flex-direction: column;
  align-items: center;
  margin-top: 5%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

.modal-center-icons {
  justify-content: center;
  align-items: center;
  display: flex;
}

.modal-wrapper {
  width: 287px;
  gap: 24px;
  margin-top: 40px;
  display: block;
}

.modal-image {
  width: 287px;
  height: 408px;
  border-radius: 8px;
  box-shadow: 3px 2px 10px rgba(0, 0, 0, .12), 3px 2px 5px rgba(0, 0, 0, .14), 3px 6px 5px rgba(0, 0, 0, .2);
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, 100%);
}

.modal-close-btn {
  width: 14px;
  height: 14px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1), stroke .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 12px;
  right: 12px;
}

.modal-close-icon {
  stroke: #111;
  width: 12px;
